import React, {Component, useContext} from 'react';
import { UserContext } from '../../stores/UserStore';
import { UserViewContext } from '../../stores/UserViewStore';
import { auth, db } from '../../utils/Firebase';
import './UserEditPage.css';

function UserEditPage() {
  const [state, updateState] = useContext(UserViewContext);
  const [userAuthState] = useContext(UserContext);

  if (!auth.currentUser) {
    return (
        <div className="user-edit-page user-page background-20 border-02">
            <span>You must be logged in to edit your user account.</span>
        </div>
    );
  }

  if (auth.currentUser.uid !== state.userId) {
    return (
        <div className="user-edit-page user-page background-20 border-02">
            <span>You cannot edit a user account that is not your pwn.</span>
        </div>
    );
  }

  return (
      <div className="user-edit-page user-page background-20 border-02">
        <div className="user-info-container background-14 border-08">
          <span type="text">Username</span>
          <input className="text-box" type="text" value={state.username} onChange={handleUsernameChanged.bind(this)}></input>
          <span type="text">Steam ID</span>
          <input className="text-box" type="text" value={state.steam} onChange={handleSteamChanged.bind(this)}></input>
          <span type="text">Discord</span>
          <input className="text-box" type="text" value={state.discord} onChange={handleDiscordChanged.bind(this)}></input>
          <span type="text">Twitch</span>
          <input className="text-box" type="text" value={state.twitch} onChange={handleTwitchChanged.bind(this)}></input>
          <span type="text">Youtube</span>
          <input className="text-box" type="text" value={state.youtube} onChange={handleYoutubeChanged.bind(this)}></input>
        </div>
        <button className="button-01" onClick={handleSaveChanges}>Save</button>
      </div>
  );

  function handleUsernameChanged(e) {
      updateState({
          type: "UPDATE_USERNAME", 
          payload: e.currentTarget.value
      });
  }

  function handleSteamChanged(e) {
      updateState({
          type: "UPDATE_STEAM", 
          payload: e.currentTarget.value
      });
  }

  function handleTwitchChanged(e) {
      updateState({
          type: "UPDATE_TWITCH", 
          payload: e.currentTarget.value
      });
  }

  function handleDiscordChanged(e) {
      updateState({
          type: "UPDATE_DISCORD", 
          payload: e.currentTarget.value
      });
  }

  function handleYoutubeChanged(e) {
      updateState({
          type: "UPDATE_YOUTUBE", 
          payload: e.currentTarget.value
      });
  }

  function handleSaveChanges() {
    if(!auth.currentUser) {
        alert('You must be logged in to save changes to your user account.')
    }
    alert('saving');

    if (userAuthState.username !== state.username) {
        db.collection('builds').where("userId", "==", auth.currentUser.uid).get().then((querySnapshot) => {
            var batch = db.batch();
            if (querySnapshot.docs.length > 0) {
                alert('got user docs to update');
                querySnapshot.docs.forEach((build) => {
                    batch.update(build.ref, {
                        username: state.username
                    });
                });
            }

            batch.set(db.collection('users').doc(auth.currentUser.uid), {
                username: state.username,
                steam: state.steam,
                discord: state.discord,
                twitch: state.twitch,
                youtube: state.youtube,
                dateModified: new Date()
            }, {merge: true});

            batch.commit().then(() => {
                alert('saved changes');
            });
        });

        return;

    }

    alert(state.discord);
    db.collection('users').doc(auth.currentUser.uid).set({
        steam: state.steam,
        discord: state.discord,
        twitch: state.twitch,
        youtube: state.youtube,
        dateModified: new Date()
    }, {merge: true}).then(() => {
        alert('updated user data')
    });
    return;

    let userRef = db.collection('users').doc(auth.currentUser.uid);
    let userBuildsRef = db.collection('builds').where("userId", "==", auth.currentUser.uid);

    db.runTransaction(async transaction => {

        let user = await transaction.get(userRef);
        let userBuilds = await transaction.get(userBuildsRef);
        alert(user.data().steam);

        if (auth.currentUser.displayName !== state.username) {
            // update the username for all of this users builds
            //var userBuilds = transaction.
        }

    }).then(() => {
        console.log('Successfully updated stats for modified build');
    }).catch((error) => {
        console.error("Error updating document: ", error);
    });
  }
}

export default UserEditPage;