import React, { useContext } from "react"
import { AppContext } from "../../stores/Store";
import { UserContext } from "../../stores/UserStore";
import BuildCreationInfo from "./BuildCreationInfo";
import BuildPatch from "./BuildPatch";
import BuildRating from "./BuildRating";
import parse from 'html-react-parser'

function BuildGuide(props) {

    if (!props.description || props.description.length === 0) {
        return null;
    } 
    return (<div className="build-guide-container divider-03 top">
        <div className="build-guide border-02 ql-editor">{parse(props.description)}</div>
    </div>);
}

export default BuildGuide;